$light-blue: #e2f3fd;
$text-dark: #003459;

figcaption {
	color: $gray-700;
	font-size: .85em;
	margin-top: ms(-2);
}

.section {
	padding: ms(4) 0;
	&__header {
		margin: 0 0 ms(1);
	}
	&__title {
		color: $text-dark;
		font-weight: bold;
		font-size: 2.2rem;
	}
}

.main-footer {
	background-color: $text-dark;
	padding: ms(1) 0;
	margin-top: auto;
	color: white;
}

.navbar {
	background-color: lighten($text-dark, 8);
	@include media-breakpoint-up(lg) {
		height: 60px;
	}
	.container {
		height: 100%;
	}
	&--fixed {
		position: fixed;
		top: 0;
		left: 0;
		z-index: $zindex-fixed;
	}
	&-brand {
		width: 100px;
		height: auto;
		margin-top: -(ms(-1));
		@include media-breakpoint-up(lg) {
			width: 170px;
			margin-top: -(ms(-2));
			padding: ms(-1) ms(1) ms(0);
			align-self: flex-start;
			border-bottom-left-radius: 5px;
			border-bottom-right-radius: 5px;
			background-color: lighten($text-dark, 20);
			box-shadow: 0 4px 15px -2px rgba(black, .5);
		}
		img {
			width: 100%;
			height: auto;
		}
	}
	&-collapse {
		justify-content: flex-end;
	}
	.nav-item {
		.nav-link {
			padding-left: ms(-1);
			padding-right: ms(-1);
		}
	}
}

.hero {
	width: 100%;
	height: auto;
	min-height: 100vh;
	padding-top: ms(2);
	padding-bottom: ms(2);
	background-image: url(./../images/hero.jpg);
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center;
	background-attachment: fixed;
	@include media-breakpoint-up(md) {
		height: 100vh;
	}
	&-container {
		width: 50%;
		height: 100%;
		margin: 0 auto;
		text-align: center;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		&--left,
		&--right {
			width: 100%;
			@include media-breakpoint-up(lg) {
				width: 50%;
			}
		}
		&--left {
			text-align: center;
		}
	}
	&-heading {
		font-size: 2.3rem;
		color: white;
		text-shadow: 0 1px 0 rgba(0, 52, 89, 0.5);
		margin-bottom: ms(5);
		@include media-breakpoint-up(lg) {
			font-size: 3rem;
		}
	}

	&--login {
		.hero {
			&-container {
				width: 100%;
				display: flex;
				flex-direction: column;
				@include media-breakpoint-up(lg) {
					flex-direction: row;
				}
			}
			&-heading {
				margin-top: ms(1);
				margin-bottom: ms(1);
			}
		}
	}
	&__form {
		width: 450px;
		margin: 0 auto;
		max-width: 98%;
		padding: ms(2) ms(1);
		text-align: left;
		background-color: rgba(white, .85);
		box-shadow: 0 7px 18px -2px rgba(0, 0, 0, 0.35);
		border-radius: 4px;
		a {
			font-weight: 300;
		}
		.form {
			&-group {
				display: flex;
				align-items: center;
				&.text-right {
					justify-content: flex-end;
				}
				&.flex-wrap {
					flex-wrap: wrap;
				}
				small {
					display: block;
					width: 100%;
					margin-top: ms(-2);
					text-align: right;
				}
				.form-label {
					display: block;
					width: 120px;
					font-size: .85rem;
					margin-bottom: 0;
					color: $gray-600;
				}
				.form-control {
					width: calc(100% - 120px);
				}
				.form-check-label {
					color: $gray-700;
				}
				.form-check-input {
					position: relative;
					top: -3px;
					margin-right: ms(-2);
				}
			}
			&-footer {
				display: flex;
				align-items: center;
				margin-top: ms(2);
				.btn {
					margin-left: auto;
				}
			}
		}
	}
}

.about {
	&__body {
		font-weight: 300;
	}
	&.product,
	&.partners {
		background-color: lighten($light-blue, 4);
	}
	figure {
		width: 50%;
		margin-bottom: ms(0);
		@include media-breakpoint-up(md) {
			width: 40%;
		}
		&.image {
			&--left {
				float: left;
				margin-right: ms(2);
			}
			&--right {
				float: right;
				margin-left: ms(2);
			}
		}
		img {
			width: 100%;
			height: auto;
			border-radius: 5px;
		}
	}
	figcaption {
		padding: 0 ms(-2);
	}
	p {
		margin-top: 0;
	}
}

.price {
	.card {
		z-index: 1;
		border-color: darken($light-blue, 4);
		&-header {
			background-color: $light-blue;
			.btn {
				color: $text-dark;
			}
		}
	}
	.table {
		tr {
			&:nth-child(even) {
				td {
					background-color: lighten($light-blue, 4);
				}
			}
		}
		td {
			&:last-of-type {
				text-align: right;
			}
		}
	}
}

.partners {
	.btn {
		font-weight: bold;
		font-size: 1.2em;
		margin-top: 2px;
		@include media-breakpoint-down(sm) {
			font-size: 1em;
		}
		span {
			display: inline-block;
			vertical-align: baseline;
			position: relative;
			top: -.2em;
			opacity: .35;
			font-size: .65em;
			margin-right: 5px;
			transform: rotate(90deg);
			transition-duration: .15s;
		}
		&.collapsed {
			span {
				transform: rotate(0deg);
			}
		}
	}
	strong {
		font-weight: 700;
	}
}

.contact-us {
	width: 90%;
	padding: ms(0);
	margin-top: ms(4);
	// background-color: lighten($light-blue, 4);
	background-color: #fff;
	border-radius: 5px;
	box-shadow: 0 4px 15px -2px rgba(black, .5);
	@include media-breakpoint-up(sm) {
		width: 65%;
		padding: ms(2);
	}
	h4,
	h5 {
		line-height: 1.6;
	}
	.alert {
		margin: ms(3) 0;
	}
	form {
		margin-top: ms(1);
		padding: ms(0);
		border: 1px solid darken($light-blue, 4);
		background-color: lighten($light-blue, 4);
		border-radius: 5px;
		@include media-breakpoint-up(sm) {
			padding: ms(2);
		}
	}
	label {
		margin-bottom: 0;
	}
}

.cabinet {
	&-container {
		display: flex;
		flex-wrap: wrap;
		align-content: flex-start;
		min-height: 100vh;
		height: auto;
		&--left,
		&--right {
			display: flex;
			flex-direction: column;
			width: 100%;
			@include media-breakpoint-up(lg) {
				width: 50%;
			}
		}
		&--right {
			margin-top: ms(2);
			@include media-breakpoint-up(lg) {
				margin-top: 0;
			}
		}
	}
	&-header  {
		display: flex;
		flex-direction: column;
		width: 100%;
		margin: ms(2) 0 ms(4);
		border-bottom: 3px solid $gray-100;
		@include media-breakpoint-up(lg) {
			flex-direction: row;
			align-items: flex-end;
			margin-top: ms(5);
		}
		&__username {
			order: 2;
		}
	}
	&-title {
		font-size: 2rem;
		order: 1;
		@include media-breakpoint-up(lg) {
			font-size: 2.4rem;
			margin-left: auto;
			order: 3;
		}
	}
	&__form {
		width: 400px;
		max-width: 96%;
		margin: 0 auto;
		& + & {
			margin-top: ms(4);
		}
		.form {
			&-group {
				display: flex;
				align-items: center;
				&.text-right {
					justify-content: flex-end;
				}
				&.flex-wrap {
					flex-wrap: wrap;
				}
				small {
					display: block;
					width: 100%;
					margin-top: ms(-2);
					text-align: right;
				}
				.form-label {
					display: block;
					width: 120px;
					font-size: .85rem;
					margin-bottom: 0;
					color: $gray-600;
				}
				.form-control {
					width: calc(100% - 120px);
					&:disabled {
						background-color: white;
						border-color: $gray-100;
					}
				}
				.form-check-label {
					color: $gray-700;
				}
				.form-check-input {
					position: relative;
					top: -3px;
					margin-right: ms(-2);
				}
			}
			&-footer {
				.btn {
					margin-left: auto;
					& + {
						.btn {
							margin-left: ms(-1);
						}
					}
				}
			}
		}
		.save-buttons,
		.edit-buttons {
			display: flex;
			align-items: center;
		}
	}
}

.card-order {
	&__title {
		margin-bottom: ms(2);
		font-size: 1.4rem;
		small {
			display: block;
			margin-top: ms(1);
			font-size: 1rem;
			font-weight: 300;
		}
	}
	ol {
		list-style-type: none;
		border: 2px solid $gray-100;
		margin: 0;
		padding: 0 ms(0);
		font-weight: 300;
		border-radius: 4px;
		@include media-breakpoint-up(md) {
			padding-left: ms(2);
			padding-right: ms(2);
		}
		p {
			margin-bottom: ms(-2);
		}
		strong {
			font-weight: 700;
		}
		& > li {
			position: relative;
			margin: ms(3) 0;
			padding-left: 2.3rem;
			&::before {
				position: absolute;
				left: 0; top: -.2rem;
				content: '1';
				width: 1.8rem; height: 1.8rem;
				line-height: 1.9rem;
				text-align: center;
				font-weight: bold;
				border-radius: 50%;
				background-color: $gray-100;
			}
			&:nth-child(2) {
				&::before {
					content: '2'
				}
			}
			&:nth-child(3) {
				&::before {
					content: '3'
				}
			}
			&.is-active {
				&::before {
					background-color: $success;
					color: white;
				}
			}
		}
		ul {
			padding-left: ms(0);
		}
	}
}

.user-statistic {
	position: relative;
	display: inline-block;
	vertical-align: middle;
	width: 400px;
	max-width: 96%;
	margin: 0 auto ms(2);
	img {
		position: absolute;
		left: 10px;
		top: 8px;
		width: 90px;
		height: auto;
		opacity: .65;
	}
	.table {
		display: block;
		width: 100%;
		font-size: 1.166rem;
		padding: 65px ms(1) ms(0);
		background-color: #08AEEA;
		background-image: linear-gradient(315deg, #08AEEA 0%, #2AF598 100%);
		border-radius: 5px;
		color: white;
		@include media-breakpoint-up(md) {
			padding-left: ms(2);
			padding-right: ms(2);
		}
	}
	&__footer {
		text-align: right;
	}
}