@import url("https://fonts.googleapis.com/css?family=Fira+Sans:300,400,700&subset=cyrillic");
.text-light-blue {
  color: #AEE6FF;
}

/* cyrillic */
@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 300;
  src: local("Open Sans Light"), local("OpenSans-Light"), url(../fonts/OpenSans-Light-Cyr.woff2) format("woff2");
  unicode-range: U+0400-045F, U+0490-0491, U+04B0-04B1, U+2116;
}

